import React, {useEffect, useState} from 'react';
import { useHistory,useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let 박스 = styled.div`
    padding : 20px;
`;

let 제목 = styled.h3`
    font-size : 25px;
    color : ${props => props.색상}
`;

function Detail(props){

    let [alert,alert변경] = useState(true);

    useEffect(()=>{
        let 타이머 = setTimeout(()=>{alert변경(false)},2000);

        return()=>{clearTimeout(타이머)}
    },[alert]);
    let {id} = useParams();
    let history = useHistory();

    

    // 원하는 상품을 정확히 찾아서 인덱싱하는법 
    // let 찾은상품 = props.shoes.find(function(상품){
    //     return 상품.id == id
    //   }); 
    return (
      <div className="container">
        <박스><제목 색상 ={'gray'}>웹툰 상세페이지</제목></박스>
        {
            alert === true
            ?<div className = "my-alert">
            <h8>다른 작품들도 보러 ㄱㄱ?</h8>
            </div>
            :null
        }
        
        <div className="row">
          <div className="col-md-6">
            <img src={props.image[id]} width="100%" />
          </div>
          <div className="col-md-6 mt-4">
            <h4 className="pt-5">{props.대표글[id]}</h4>
            <p>네이버웹툰</p>
            <button className="btn btn-danger" onClick={()=>{window.location.href = props.weblink[id]}}>보러가기</button> 
            &nbsp;&nbsp;
            <button className="btn btn-danger" onClick={()=>{history.goBack()}}>뒤로가기</button> 
          </div>
        </div>
  </div>
  
    )
}

export default Detail;